import 'firebase/app'
import 'firebase/messaging'
import jQuery from 'jquery'

export default {
  data(){
    return {
      messaging:{}
    }
  },
  methods: {
    async saveNotificationToken(token) {
      if( !(this.$heap.state.user_id>0) ){
        this.$flash("Необходимо авторизироваться")
        this.$router.push("sign-in")
        return
      }
      try{
        const request={
          user_id:this.$heap.state.user_id,
          type:'web',
          registration_id:token
        }
        const response=await jQuery.post(`${this.$heap.state.hostname}Message/recieverCreate`,request)
        console.log('saveNotificationToken',response)
      }catch(err){
        console.log(err)
      }
    },
    async subscribe(){
      try{
        const permission=await Notification.requestPermission()
        console.log(permission,'Notification permission granted.')

        console.log(this.messaging)
        const token=await getToken({vapidKey: "BLzv4oK1EGxAxwPtlAXvMNzaYvMJK1qvx92D0f1U89wlUPxUU4gAtes7c__wcTwvmEyds6e7-p0Oy4bVYZo1Igg"});
        this.saveNotificationToken(token)
      }catch(err){
        this.$alert("Не удалось подключить уведомления")
        console.log('Unable to get permission to notify.', err)
      }
    }
  },
  mounted() {
    const firebaseConfig = {
      apiKey: "AIzaSyDHDeSPsSoJHE_HYKQ_vgOvSfJIN_8Y2Uc",
      authDomain: "tezkel-8e371.firebaseapp.com",
      projectId: "tezkel-8e371",
